/* 
Sticky footer styles
(https://getbootstrap.com/docs/4.0/examples/sticky-footer/)
*/

html {
    position: relative;
    min-height: 100%;
}

body {
    margin-bottom: 80px; /* 50px + 30px (height + margin) */
}

.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 50px;
    margin-top: 30px;
    background-color: #f5f5f5;
}
  